import { useForm } from 'react-hook-form';
import { useState } from 'react';
import styled, { createGlobalStyle } from 'styled-components';
import { Form, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import emailjs from '@emailjs/browser';
import { useMediaQuery } from 'react-responsive';

function ApplyForm() {
  const [tALength, setTALength] = useState('');
  const [isSent, setIsSent] = useState(false);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const GlobalStyle = createGlobalStyle`
    html, body{
      font-family: 'Dongle', sans-serif;
      padding : 0;
      font-size : 20px;
    }
  `;

  const sendApplication = (data) => {
    emailjs.send(
      'service_whka7id',
      'template_zko9o7d',
      data,
      'CGu8t6QHOde4ocUiq'
    );
  };

  const isMobile = useMediaQuery({
    query: `( max-width: 500px)`,
  });
  const isDesktop = useMediaQuery({ query: `(min-width: 501px)` });

  return (
    <>
      <GlobalStyle />
      {isSent === true ? (
        <FormDiv>
          <p>  .</p>
          <p>     . .</p>
        </FormDiv>
      ) : (
        <>
          {isDesktop && (
            <FormDiv>
              <Form
                onSubmit={handleSubmit((data) => {
                  sendApplication(data);
                  setIsSent(true);
                })}
              >
                <TitleDiv>
                  <h1 style={{ margin: 0 }}> </h1>
                </TitleDiv>
                <FormDivMain>
                  <WritingDiv>
                    <LabelText htmlFor="name"></LabelText>
                    <FormInputS
                      id="name"
                      {...register('name', { required: true })}
                      placeholder=" ."
                    />
                    {errors.name?.type === 'required' && (
                      <ErrorMessage role="alert">
                         .
                      </ErrorMessage>
                    )}
                  </WritingDiv>
                  <WritingDiv>
                    <LabelText htmlFor="age"></LabelText>
                    <FormInputS
                      id="age"
                      {...register('age', { required: true })}
                      placeholder=" . *   !"
                    />
                    {errors.age?.type === 'required' && (
                      <ErrorMessage role="alert">
                         .
                      </ErrorMessage>
                    )}
                  </WritingDiv>
                  <WritingDiv>
                    <LabelText htmlFor="major"></LabelText>
                    <FormInputS
                      id="major"
                      {...register('major', { required: true })}
                      placeholder="   ."
                    />
                    {errors.major?.type === 'required' && (
                      <ErrorMessage role="alert">
                         .
                      </ErrorMessage>
                    )}
                  </WritingDiv>
                  <WritingDiv>
                    <LabelText htmlFor="major"></LabelText>
                    <FormInputS
                      id="studentNumber"
                      {...register('studentNumber', { required: true })}
                      placeholder=" ."
                    />
                    {errors.studentNumber?.type === 'required' && (
                      <ErrorMessage role="alert">
                         .
                      </ErrorMessage>
                    )}
                    {}
                  </WritingDiv>
                  <WritingDiv>
                    <LabelText htmlFor="hobby">   </LabelText>
                    <FormInputS
                      id="hobby"
                      {...register('hobby', { required: true })}
                      placeholder="    ."
                    />
                    {errors.hobby?.type === 'required' && (
                      <ErrorMessage role="alert">
                         .
                      </ErrorMessage>
                    )}
                  </WritingDiv>
                  <WritingDiv>
                    <LabelText htmlFor="where">  </LabelText>
                    <br />
                    <CheckBox
                      type="checkbox"
                      {...register('where', { required: true })}
                      value=""
                      label=""
                    />
                    <CheckBox
                      type="checkbox"
                      {...register('where', { required: true })}
                      value=""
                      label=""
                    />

                    {errors.where?.type === 'required' && (
                      <ErrorMessage role="alert">
                         .
                      </ErrorMessage>
                    )}
                  </WritingDiv>
                  <WritingDiv>
                    <LabelText htmlFor="call"></LabelText>
                    <FormInputS
                      id="call"
                      {...register('call', { required: true })}
                      placeholder="   ID "
                    />
                    {errors.call?.type === 'required' && (
                      <ErrorMessage role="alert">
                         .
                      </ErrorMessage>
                    )}
                  </WritingDiv>

                  <WritingDiv>
                    <LabelText htmlFor="introduce">
                          
                    </LabelText>
                    <DivTextarea>
                      <FormTextarea
                        as="textarea"
                        id="introduce"
                        maxLength="400"
                        rows="10"
                        cols="50"
                        {...register('introduce', { required: true })}
                        placeholder=" 400  .
                     ."
                        onChange={(e) => setTALength(e.target.value.length)}
                      />
                      <span>{tALength === '' ? 0 : tALength} / 400</span>
                      <br />
                      {errors.introduce?.type === 'required' && (
                        <ErrorMessage role="alert">
                           .
                        </ErrorMessage>
                      )}
                    </DivTextarea>
                  </WritingDiv>
                  <SubmitBtn type="submit"></SubmitBtn>
                </FormDivMain>
              </Form>
            </FormDiv>
          )}
          {isMobile && (
            <MFormDiv>
              <Form
                onSubmit={handleSubmit((data) => {
                  sendApplication(data);
                  setIsSent(true);
                })}
              >
                <MTitleDiv>
                  <h1 style={{ margin: 0 }}> </h1>
                </MTitleDiv>
                <MFormDivMain>
                  <MWritingDiv>
                    <MLabelText htmlFor="name"></MLabelText>
                    <MFormInputS
                      id="name"
                      {...register('name', { required: true })}
                      placeholder=" ."
                    />
                    {errors.name?.type === 'required' && (
                      <MErrorMessage role="alert">
                         .
                      </MErrorMessage>
                    )}
                  </MWritingDiv>
                  <MWritingDiv>
                    <MLabelText htmlFor="age"></MLabelText>
                    <MFormInputS
                      id="age"
                      {...register('age', { required: true })}
                      placeholder="  *   !"
                    />
                    {errors.age?.type === 'required' && (
                      <MErrorMessage role="alert">
                         .
                      </MErrorMessage>
                    )}
                  </MWritingDiv>
                  <MWritingDiv>
                    <MLabelText htmlFor="major"></MLabelText>
                    <MFormInputS
                      id="major"
                      {...register('major', { required: true })}
                      placeholder="  "
                    />
                    {errors.major?.type === 'required' && (
                      <MErrorMessage role="alert">
                         .
                      </MErrorMessage>
                    )}
                  </MWritingDiv>
                  <MWritingDiv>
                    <MLabelText htmlFor="major"></MLabelText>
                    <MFormInputS
                      id="studentNumber"
                      {...register('studentNumber', { required: true })}
                      placeholder=""
                    />
                    {errors.studentNumber?.type === 'required' && (
                      <MErrorMessage role="alert">
                         .
                      </MErrorMessage>
                    )}
                    {}
                  </MWritingDiv>
                  <MWritingDiv>
                    <MLabelText htmlFor="hobby">   </MLabelText>
                    <MFormInputS
                      id="hobby"
                      {...register('hobby', { required: true })}
                      placeholder="   "
                    />
                    {errors.hobby?.type === 'required' && (
                      <MErrorMessage role="alert">
                         .
                      </MErrorMessage>
                    )}
                  </MWritingDiv>
                  <MWritingDiv>
                    <MLabelText htmlFor="where">  </MLabelText>
                    <br />
                    <CheckBox
                      type="checkbox"
                      {...register('where', { required: true })}
                      value=""
                      label=""
                    />
                    <CheckBox
                      type="checkbox"
                      {...register('where', { required: true })}
                      value=""
                      label=""
                    />

                    {errors.where?.type === 'required' && (
                      <MErrorMessage role="alert">
                         .
                      </MErrorMessage>
                    )}
                  </MWritingDiv>
                  <MWritingDiv>
                    <MLabelText htmlFor="call"></MLabelText>
                    <MFormInputS
                      id="call"
                      {...register('call', { required: true })}
                      placeholder="   ID"
                    />
                    {errors.call?.type === 'required' && (
                      <MErrorMessage role="alert">
                         .
                      </MErrorMessage>
                    )}
                  </MWritingDiv>

                  <MWritingDiv>
                    <MLabelText htmlFor="introduce">
                          
                    </MLabelText>
                    <MDivTextarea>
                      <MFormTextarea
                        as="textarea"
                        id="introduce"
                        maxLength="400"
                        rows="10"
                        cols="50"
                        {...register('introduce', { required: true })}
                        placeholder=" 400  .   ."
                        onChange={(e) => setTALength(e.target.value.length)}
                      />
                      <span style={{ fontSize: '7px' }}>
                        {tALength === '' ? 0 : tALength} / 400
                      </span>
                      <br />
                      {errors.introduce?.type === 'required' && (
                        <MErrorMessage role="alert">
                           .
                        </MErrorMessage>
                      )}
                    </MDivTextarea>
                  </MWritingDiv>
                  <MSubmitBtn type="submit">   </MSubmitBtn>
                </MFormDivMain>
              </Form>
            </MFormDiv>
          )}
        </>
      )}
    </>
  );
}

export default ApplyForm;

//////// Desktop
const FormDiv = styled.div`
  background-color: #bac6f1;
  height: 100%;
  padding: 20px;
  display: flex;
  justify-content: center;
`;

const TitleDiv = styled.div`
  text-align: center;
  margin-bottom: 20px;
  font-size: 50px;
`;

const FormDivMain = styled.div`
  background-color: white;
  display: grid;
  justify-content: center;
  width: 700px;
  padding-top: 50px;
  border-radius: 25px;
`;

const LabelText = styled(Form.Label)`
  font-size: 30px;
`;

const WritingDiv = styled.div`
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
`;

const FormInputS = styled(Form.Control)`
  border: none;
  border: 1px solid palevioletred;
`;

const FormTextarea = styled(Form.Control)`
  border: none;
  border: 1px solid palevioletred;
  padding: 5px;
  margin-right: 7px;
  border-radius: 7px;
  width: 400px;
`;

const DivTextarea = styled.div`
  // display: flex;
  // align-items: end;
`;

const ErrorMessage = styled.p`
  margin-top: 10px;
  color: red;
`;

const SubmitBtn = styled(Button)`
  color: palevioletred;
  font-size: 1em;
  margin: 1em auto;
  padding: 0.25em 1em;
  border: 2px solid palevioletred;
  border-radius: 20px;
  background-color: white;
  width: 520px;
  height: 50px;
  font-weight: bold;
`;

const CheckBox = styled(Form.Check)`
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  input {
    margin: 0 5px 1px 0;
  }
`;

//////// Mobile
const MFormDiv = styled(FormDiv)`
  background-color: #bac6f1;
  height: 100%;
  padding: 20px;
  display: flex;
  width: 100vw;
  justify-content: center;
`;

const MTitleDiv = styled(TitleDiv)`
  text-align: center;
  margin-bottom: 20px;
  font-size: 40px;
`;

const MFormDivMain = styled(FormDivMain)`
  background-color: white;
  display: grid;
  justify-content: center;
  width: 350px;
  padding-top: 50px;
  border-radius: 25px;
`;

const MLabelText = styled(LabelText)`
  font-size: 20px;
  margin-left: 5px;
`;

const MWritingDiv = styled(WritingDiv)`
  margin-bottom: 10px;
  padding-bottom: 5px;
  margin-left: 15%;
  // width: 300px;
  width: 70%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
`;

const MFormInputS = styled(FormInputS)`
  border: none;
  border: 1px solid palevioletred;
  width: 90%;
  font-size: 20px;
`;

const MFormTextarea = styled(FormTextarea)`
  border: none;
  border: 1px solid palevioletred;
  padding: 5px;
  margin-right: 7px;
  border-radius: 7px;
  width: 80%;
  font-size: 13px;
`;

const MDivTextarea = styled(DivTextarea)`
  // display: flex;
  // align-items: end;
`;

const MErrorMessage = styled(ErrorMessage)`
  margin-top: 5px;
  color: red;
`;

const MSubmitBtn = styled(SubmitBtn)`
  color: palevioletred;
  font-size: 1em;
  margin: 1em auto;
  padding: 0.25em 1em;
  border: 2px solid palevioletred;
  border-radius: 20px;
  background-color: white;
  width: 180px;
  height: 45px;
  font-weight: bold;
`;
